<template>
  <div>
    <p class="text-4xl text-MyColor-H3 font-bold pt-4">資料結構</p>
    <p class="text-3xl text-MyColor-H5">注意事項</p>
    <br />
    <ol class="text-left list-decimal list-inside Indent">
      <li>稱為DataStructure、資結</li>
      <li>是一種資料的存放思維或是方式</li>
      <li>高中基本上只有接觸到線性的資料結構</li>
      <li>大學會學到非線性資料結構</li>
      <li>以樹狀資料結構、圖狀資料結構最為難，但高中根本不會考</li>
      <li>
        常見名詞List、Tuple、Array、Dictionary、String、Tree、Set、Binary
        Tree、Map、HashSet、HashTable
      </li>
      <li>其實很多名詞都是指同個東西只是換個語言說法不同</li>
      <li>線性資料結構有:String、List、Array、Queue、Stack</li>
      <li>其他結構基本上95%都是非線性</li>
      <li>資料結構比較早講是因為PyThon的For迴圈是依賴於Iterable(迭代物件)</li>
      <li>
        String、List、Queue、Stack、Dictionary、Set在Python世界裡面都是Iterable即可以跑For迴圈
      </li>
      <li>迭代中文意思是指按照次序輪流更替的代入直到沒有東西可以代入為止</li>
      <li>以下代碼基本上都是PyThon，PyThon是透過[]存取而VB是()沒有任何例外</li>
      <li>許多非線性資料結構是可以用線性資料結構寫出來只是尋找的方式不同</li>
      <li>並非每個資料結構，每個語言都有，當沒有的時候會需要自己手寫</li>
    </ol>
    <br />
    <hr />
    <p class="text-3xl text-MyColor-H5">Array</p>
    <br />
    <ol class="text-left list-decimal list-inside Indent">
      <li>台灣翻譯陣列，大陸翻譯數組</li>
      <li>屬於線性資料結構</li>
      <li>代表記憶體的連續空間</li>
      <li>透過註標(index)進行指定位置</li>
      <li>搜尋元素的方式是透過Head位址+index數字*資料型態</li>
      <li>所以搜尋速度很快非常快</li>
      <li>裡面元素可以變動</li>
      <li>長度不可以變動</li>
      <li>有真二維陣列和假二維陣列</li>
      <li>
        不規則二維陣列即假二維陣列，因為真二維陣列是個長方形不是多個長方形組成
      </li>
      <li>假二維陣列是指陣列裡面再陣列</li>
      <li>&lcub;&emsp;&lcub;20&rcub;, &lcub;30, 20&rcub; &rcub;</li>
      <li>存取方式a(Row的index)(Col的index)</li>
      <li>真二維陣列</li>
      <li>&lcub;&emsp;&lcub;20, 10&rcub;, &lcub;30, 20&rcub; &rcub;</li>
      <li>存取方式a(Row的index, Col的index)</li>
      <li>PyThon沒有</li>
    </ol>
    <br />
    <img
      class="mx-auto"
      src="https://beginnersbook.com/wp-content/uploads/2018/10/array.jpg"
      alt=""
    />
    <br />
    <ol class="text-left list-none list-inside Indent">
      <li><code>Dim A(4) as String</code></li>
      <li>宣告一個String陣列長度為5</li>
      <li>即0, 1, 2, 3, 4共五個位置</li>
      <li><code>A = {"ff", "dd", "gg", "cc"}</code></li>
      <li>對A賦值一組陣列</li>
      <li>此時A(0)代表第一個即"ff"</li>
      <li>此時A(1)代表第二個即"dd"</li>
      <li><code>Array.Sort(A)</code></li>
      <li>對A進行排序</li>
      <li>A會變成{"cc", "dd", "ff", "gg"} 因為用Ascii進行排序</li>
      <li><code>Array.Reverse(A)</code></li>
      <li>對A進行反轉</li>
      <li>從{"cc", "dd", "ff", "gg"}變成{"gg", "ff", "dd", "cc"}</li>
    </ol>
    <br />
    <hr />
    <p class="text-3xl text-MyColor-H5">List</p>
    <br />
    <ol class="text-left list-decimal list-inside Indent">
      <li>台灣翻譯串列，大陸翻譯串列</li>
      <li>全名Linked List</li>
      <li>屬於線性資料結構</li>
      <li>代表記憶體的多個空間(即可以不連續)</li>
      <li>透過註標(index)進行指定位置</li>
      <li>
        搜尋元素的方式是透過Head指向的Node來當下一次迴圈的Head直到跑完指定迴圈數(index的數字)所指向的Node
      </li>
      <li>搜尋速度算緩慢有n個就要跑O(n)圈</li>
      <li>big-O是指n+1圈 = n圈 3n^2+4n+3 = n^2圈 即取最大指數但不取係數</li>
      <li>有些語言跑n圈，有些語言跑n-1圈通稱O(n)圈</li>
      <li>長度可以變動</li>
      <li>元素可以變動</li>
      <li>只有假二維串列</li>
      <li>串列的元素是串列即假二維串列</li>
      <li>比如[ [10], [20, 30] ]</li>
      <li>存取方式A[Row的index][Col的index]</li>
      <li>PyThon允許進行+和*</li>
      <li>PyThon和VB都有，但是VB沒教過也不會考</li>
    </ol>
    <br />
    <img
      class="mx-auto"
      src="https://toolsqa.com/wp-content/uploads/sites/1/nggallery/data-structures/thumbs/thumbs_linked-list-traverse-1.png"
      alt=""
    />
    <br />
    <ol class="text-left list-none list-inside Indent">
      <li><code>A = [10, 20, 15]</code></li>
      <li>對a賦值一個長度為3的串列</li>
      <li>即0, 1, 2共兩個位置</li>
      <li><code>A[0]</code></li>
      <li>第一個位置是10</li>
      <li><code>A[1]</code></li>
      <li>第二個位置是20</li>
      <li><code>A[-1]</code></li>
      <li>Right(右邊或說後面看過來)第一個位置是15</li>
      <li><code>A[-2]</code></li>
      <li>Right(右邊或說後面看過來)第二個位置是20</li>
      <li><code>A.sort()</code></li>
      <li>對A進行排序</li>
      <li>A會變成[10, 15, 20]</li>
      <li><code>A.reverse()</code></li>
      <li>對A進行反轉</li>
      <li>從[10, 15, 20]變成[20, 15, 10]</li>
      <li><code>A.index(10)</code></li>
      <li>A是[20, 15, 10]</li>
      <li>回傳2 從左邊開始尋找直到找到10並回傳該Index</li>
      <li><code>A.rindex(10)</code></li>
      <li>回傳2 從右邊開始尋找直到找到10並回傳該Index</li>
      <li><code>A.append(99)</code></li>
      <li>在後面添加99即[20, 15, 10, 99]</li>
      <li><code>A.pop(2)</code></li>
      <li>
        取出index2的位置獲得10這個值並移除掉串列該元素的位置即[20, 15, 99]
      </li>
      <li><code>2 in A</code></li>
      <li>
        回傳 2有在a裡面 成不成立這程式碼回傳False
      </li>
      <li><code>2 not in A</code></li>
      <li>
        回傳 2沒有在a裡面 成不成立這程式碼回傳True
      </li>
      <li><code>A *= 2</code></li>
      <li>[20, 15, 99, 20, 15, 99]</li>
      <li><code>A += [20]</code></li>
      <li>[20, 15, 99, 20, 15, 99, 20]</li>
    </ol>
    <br />
    <hr />
    <p class="text-3xl text-MyColor-H5">String</p>
    <br />
    <ol class="text-left list-decimal list-inside Indent">
      <li>台灣翻譯字串，大陸翻譯字符串</li>
      <li>屬於線性資料結構</li>
      <li>代表記憶體的連續空間</li>
      <li>可以透過註標(index)進行指定位置印出字元，或直接印出多的字元的串接</li>
      <li>
        搜尋元素的方式是透過Head指向的Node來當下一次迴圈的Head直到跑完指定迴圈數(index的數字)所指向的Node
      </li>
      <li>搜尋速度算緩慢有n個就要跑O(n)圈</li>
      <li>長度可以變動</li>
      <li>每次變動長度會創建新的連續性記憶體空間</li>
      <li>元素不可以變動</li>
      <li>維度始終只能是一</li>
      <li>PyThon允許進行+和*</li>
      <li>無法進行-運算</li>
      <li>
        EX:"Apple" - "Banana"
        是無法得知結果(可以理解數字是絕對的而字串相減是相對的)
      </li>
      <li>10 - 9 =1</li>
      <li>
        "房子" - "木頭" 可能有人覺得是石頭房或是磚頭房，沒有一個絕對的答案
      </li>
      <li>純粹改字有其他內建函式可以提供取代指定字</li>
      <li>存取方式A[Col的index]</li>
      <li>
        PyThon和VB都有，但是VB指定位置回傳的是字元而PyThon則是統一字串不存在字元這個Type
      </li>
    </ol>
    <br />
    <img
      class="mx-auto"
      src="https://www.tutorialspoint.com/cprogramming/images/string_representation.jpg"
      alt=""
    />
    <br />
    <ol class="text-left list-none list-inside Indent">
      <li><code>A = " Abu g "</code></li>
      <li>對a賦值一個長度為7的字串</li>
      <li>即0, 1, 2, 3共七個位置</li>
      <li><code>A[0]</code></li>
      <li>" "</li>
      <li><code>A[1]</code></li>
      <li>"A"</li>
      <li><code>A[-1]</code></li>
      <li>Right(右邊或說後面看過來)第一個位置是" "</li>
      <li><code>A[-2]</code></li>
      <li>Right(右邊或說後面看過來)第二個位置是"g"</li>
      <li><code>A.lstrip()</code></li>
      <li>回傳 A移除左邊ALL空白</li>
      <li>"Abu g "</li>
      <li><code>A.rstrip()</code></li>
      <li>回傳 A移除右邊ALL空白</li>
      <li>" Abu g"</li>
      <li><code>A = A.strip()</code></li>
      <li>對A賦值 A移除左右邊ALL空白</li>
      <li>A會變成"Abu g"</li>
      <li>
        strip系列只會移除左邊和右邊的空白直到兩邊變成非空白也可以代參數進去移除
      </li>
      <li>即A.strip(n) 移除左右兩邊的n元素直到出現非n元素的元素為止</li>
      <li><code>A.upper()</code></li>
      <li>回傳 A轉大寫</li>
      <li>"ABU G"</li>
      <li><code>A.lower()</code></li>
      <li>回傳 A轉小寫</li>
      <li>"abu g"</li>
      <li><code>A.split()</code></li>
      <li>回傳 A字串中遇到空白就切分的List</li>
      <li>["Abu", "g"]</li>
      <li>也可以代參數變成遇到什麼就切分比如</li>
      <li><code>A.split("b")</code></li>
      <li>回傳 A字串中遇到"b"就切分的List</li>
      <li>["A", "u g"]</li>
      <li>
        取出index2的位置獲得10這個值並移除掉串列該元素的位置即[20, 15, 99]
      </li>
      <li><code>"d" in A</code></li>
      <li>
        回傳 "d"有在a裡面 成不成立這程式碼回傳False
      </li>
      <li><code>"d" not in A</code></li>
      <li>
        回傳 "d"沒有在a裡面 成不成立這程式碼回傳True
      </li>
    </ol>
    <br />
    <hr />
    <p class="text-3xl text-MyColor-H5">Tuple</p>
    <br />
    <ol class="text-left list-decimal list-inside Indent">
      <li>台灣翻譯元組，大陸翻譯元組</li>
      <li>注意老師講義寫法是 數組(Array和Tuple差很多)</li>
      <li>代表記憶體的連續空間</li>
      <li>可以透過註標(index)進行指定位置印出</li>
      <li>搜尋元素的方式是透過Head位址+index數字*資料型態</li>
      <li>所以搜尋速度很快非常快</li>
      <li>元素不可以變動(這就是和Array最大差別)</li>
      <li>如果裡面元素是List則該List可以變動</li>
      <li>長度不可以變動</li>
      <li>PyThon允許進行+和*</li>
      <li>每次進行+或是*都會產生新的連續性記憶體位置</li>
      <li>存取方式A[Col的index]</li>
      <li>
        PyThon有，VB沒有
      </li>
    </ol>
    <br />
    <img
      class="mx-auto"
      src="https://o7planning.org/en/11435/cache/images/i/7752828.png"
      alt=""
    />
    <br />
    <ol class="text-left list-none list-inside Indent">
      <li><code>A = (20, 15)</code></li>
      <li>注意(a)不是元組，是優先符即()內先做</li>
      <li>(a, b)才是元組</li>
      <li>即()中出現,且不是呼叫函式的()</li>
      <li><code>A[1]</code></li>
      <li>"15"</li>
      <li><code>A[-1]</code></li>
      <li>Right(右邊或說後面看過來)第一個位置是15</li>
      <li><code>A[-2]</code></li>
      <li>Right(右邊或說後面看過來)第二個位置是20</li>
      <li>本身只有1 ~ 2個函式操作且考試不會用到這些函式</li>
      <li>隱含使用範例</li>
      <li><code>a, b = b, a</code></li>
      <li>即兩個變數交換 會被轉換成下面代碼</li>
      <li><code>temp = (b,a)</code></li>
      <li><code>a = temp[0]</code></li>
      <li><code>b = temp[1]</code></li>
      <li>傳統變數交換是</li>
      <li><code>temp = a</code></li>
      <li><code>a = b</code></li>
      <li><code>b = temp</code></li>
      <li>隱含使用方是會比較快在Python世界中</li>
      <li class="italic underline">精華陷阱題!!!!!!!!</li>
      <li><code>mTuple = ([10], [20])</code></li>
      <li>即Tuple裡面是List</li>
      <li><code>mTuple[0] = 10</code></li>
      <li>會報錯因為Tuple不能修改只能讀取</li>
      <li><code>mTuple[0][0] = 30</code></li>
      <li>不會報錯因為List可以修改</li>
      <li>此時mTuple會變成 ([30], [20])</li>
      <li><code>mTuple[1].append(99)</code></li>
      <li>不會報錯因為List可以修改</li>
      <li>此時mTuple會變成 ([30], [20, 99])</li>
      <li>因為mTuple[x]回傳的是List</li>
      <li>所以我們實際上是在修改那個位置的List但mTuple那個位置還是那個List</li>
      <li>
        比如說房子是A先生和B小姐的名下，有天B小姐整容變了樣子，可她還是B小姐，擁有這棟房子
      </li>
    </ol>
    <br />
    <hr />
    <p class="text-3xl text-MyColor-H5">Dictionary</p>
    <br />
    <ol class="text-left list-decimal list-inside Indent">
      <li>台灣翻譯字典，大陸翻譯字典</li>
      <li>別稱HashTable(雜湊表)，有些語言叫做Map</li>
      <li>非線性資料結構</li>
      <li>是Key-Value的組合</li>
      <li>線性資料結構是Index-Value的組合</li>
      <li>把Key代入HashFunction(雜湊函式)回傳的結果即記憶體位址</li>
      <li>這個過程稱為Mapping(映射)，而記憶體位置稱為(映射位置)</li>
      <li>再把記憶體位址的值(Value)回傳</li>
      <li>搜尋元素的方式是跑一個Function</li>
      <li>所以搜尋速度取決於雜湊函式的複雜度</li>
      <li>元素可以變動</li>
      <li>可以隨意添加Key或是移除Key</li>
      <li>存取方式A[Key]</li>
      <li>無法取得位定義Key的Value，但可以對未定義的Key的映射位置進行賦值</li>
      <li>
        PyThon和VB都有，但VB不會考
      </li>
    </ol>
    <br />
    <img
      class="mx-auto bg-white-100"
      src="https://upload.wikimedia.org/wikipedia/commons/thumb/7/7d/Hash_table_3_1_1_0_1_0_0_SP.svg/630px-Hash_table_3_1_1_0_1_0_0_SP.svg.png"
      alt=""
    />
    <br />
    <ol class="text-left list-none list-inside Indent">
      <li><code>A = { }</code></li>
      <li>或是</li>
      <li><code>A = { "A":"adcc"}</code></li>
      <li>不能是{"a"}</li>
      <li>格式{ key:value }</li>
      <li>PyThon字典跑迴圈是回傳Key 越先定義的Key越先印出來</li>
      <li><code>A["ff"] = "gdc"</code></li>
      <li>可以這樣對未定義的Key賦值</li>
      <li><code>A["ff"] += "dde"</code></li>
      <li>A["ff"]會變成"gdcdde"</li>
      <li>即可以修改元素</li>
      <li><code>A["dd"] *= 10</code></li>
      <li>會爆錯因為</li>
      <li><code>A["dd"] = A["dd"] * 10</code></li>
      <li>再A["dd"] * 10的時候找不到A["dd"]的Value</li>
      <li><code>A.get("gbb","找不到")</code></li>
      <li>如果A裡面存在"gdd"這個Key回傳對應的Value否則回傳"找不到"</li>
    </ol>
    <br />
    <hr />
    <p class="text-3xl text-MyColor-H5">Set</p>
    <br />
    <ol class="text-left list-decimal list-inside Indent">
      <li>台灣翻譯集合，大陸翻譯集合</li>
      <li>別稱HashSet(雜湊集合)</li>
      <li>非線性資料結構</li>
      <li>本質上不存在讀取方式</li>
      <li>用於判斷迭代對象與集合的元素關係</li>
      <li>元素可以變動</li>
      <li>偏數學</li>
      <li>不允許重複元素 即{20, 20, 30}會變成{20, 30}</li>
      <li>
        PyThon和VB都有，但VB不會考
      </li>
    </ol>
    <br />
    <img
      class="mx-auto bg-white-100"
      src="http://imgur.com/HIeaLY4.png"
      alt=""
    />
    <br />
    <ol class="text-left list-none list-inside Indent">
      <li><code>A = {"A"}</code></li>
      <li>或是</li>
      <li><code>A = set()</code></li>
      <li>不能是{"a":15}</li>
      <li>PyThon集合跑迴圈是回傳元素 越先定義的元素越先印出來</li>
      <li><code>A.add(15)</code></li>
      <li>集合中新增15元素</li>
      <li><code>A.remove(15)</code></li>
      <li>移除集合中的15元素，假設沒有該元素則報錯</li>
      <li><code>A.difference(b)</code></li>
      <li><code>同A - b</code></li>
      <li>求差集(我有你沒有)</li>
      <li><code>A.intersection(b)</code></li>
      <li><code>同A & b</code></li>
      <li>求交集(我有你也有)</li>
      <li><code>A.union(b)</code></li>
      <li><code>同A | b</code></li>
      <li>求聯集(我有或你有)</li>
      <li><code>A.symmetric_difference(b)</code></li>
      <li><code>同A ^ b</code></li>
      <li>求對稱交集(我有或你有，但不含我們都有)</li>
    </ol>
    <br />
    <hr />
    <p class="text-3xl text-MyColor-H5">Queue</p>
    <br />
    <ol class="text-left list-decimal list-inside Indent">
